.App {
    background: url('https://i.imgur.com/tE9vNJj.png');
    min-height: 100vh;
    display: grid;
    grid-template-columns: 1fr 200px minmax(auto, 600px) 1fr;
    grid-template-rows: 100px auto;
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    text-align: justify;
    -ms-flex-align: stretch;
        align-items: stretch;
}

.App-navigation {
    background: rgba(255, 255, 255, 0.9);
    padding: 10px;
}

.App-sidebar {
    background: rgba(255, 255, 255, 0.9);
    display: relative;
    padding: 10px;
}
.App-mainContent {
    padding: 10px;
    background: rgba(255, 255, 255, 0.9);
}

.App-sidebar img {
    max-width: 100%;
}

.App-sidebar a {
    display: block;
    font-size: 24pt;
    text-decoration: none;
    color: olive;
    font-weight: bold;
    text-shadow: 0 0 3px olive;
}

.App-sidebar a:hover {
    text-shadow: 0 0 10px olive;
}

.App-navigation h1 {
    font-size: 36pt;
}

.Article-title {
    font-size: 2em;
}

.App-navigation {
    grid-column: 2 / span 2;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
}


.App-navigation a {
    display: block;
    font-size: 18pt;
    text-decoration: none;
    color: white;
    font-weight: bold;
    background: tomato;
    padding: 5px;
    -webkit-box-shadow: 0 0 10px rgba(255, 255, 255, 0.5) inset;
            box-shadow: 0 0 10px rgba(255, 255, 255, 0.5) inset;
    -webkit-transition: background-color 0.1s;
    -o-transition: background-color 0.1s;
    transition: background-color 0.1s;
}

.App-navigation a:hover {
    background: pink;
}

.App-sidebar {
    grid-column: 2;
    grid-row: 2;
}

.App-mainContent {
    grid-column: 3;
}

.App-mainContent h1 {
  font-size: 32pt;
  color: tomato;
  margin-bottom: 20px;
}



